<template>
  <div>
    Find a way to put these {{ words.length }} rhyming words together!
    <br>
    Your Words:
    <br>
    <span v-for="word in words" :key="word.id">
      <span v-if="words[words.length - 1] === word">{{ ', and ' }}</span>
      <span v-else-if="words[0] !== word">{{ ', ' }}</span>
      <span class="word">{{ word }}</span>
    </span>
    <br>
    <b>Waiting On:</b>
    <div v-for="player in notAnswered" :key="player.id">
      {{ player }}
    </div>
    <br>
    <b>Players Who Have Responded:</b> 
    <div v-for="response in responseArray" :key="response.id">
      {{ response }} {{ promptResponses[response] }}
    </div>
    <br>
    <v-btn 
      @click.stop="$emit('change-view', 'vote')" 
      :disabled="!!notAnswered.length"
    >Start Voting</v-btn>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // list of players who have responded
      responseArray: [],
      // words in current prompt
      words: [],
      // array containing players that have not answered the prompt
      notAnswered: []
    }
  },
  mounted() {
    const RHYMING_PAIRS = [
      ['ground', 'pound', 'round'], 
      ['crown', 'grown'], 
      ['paid', 'afraid', 'laid'], 
      ['elaborate', 'vacate'],
      ['fvmo', 'w', 'sdf', 'fw', 'wwv']
    ]
    this.words = RHYMING_PAIRS[Math.floor(Math.random() * RHYMING_PAIRS.length)]
    // for experimental purposes
    // this.words = ['gvvd', 'cwd', 'cw','cw','dcvv', 'sdcvfeve', 'vwsve']
    this.socketInstance.emit('new-words', this.words)
  },
  props: {
    promptResponses: {
      type: Object,
      required: true
    },
    playerList: {
      type: Array,
      require: true
    },
    socketInstance: {
      required: true
    }
  },
  watch: {
    promptResponses: {
      immediate: true,
      handler(v) {
        this.responseArray = Object.keys(v)
        this.notAnswered = this.playerList.filter((player) => {
          return !this.responseArray.includes(player)
        })
      }
    }
  }
}
</script>

<style scoped>
.word {
  color: red;
  font-style: italic;
}
</style>